.task {

  .main__block {
    &:hover {
      @media only screen and (min-width: 1280px) {
        box-shadow: 0 15px 30px @black-rgba-two;
        transform: translateY(-2px) scale(1.02);
      }
    }
  }

  & .link {
    font-size: 20px;
  }

  &__body {
    @media only screen and (max-width: 600px) {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__footer {
    border-top: 1px solid @color-green;
    color: @grey-one;
    transition: .3s ease-in-out;
    margin: 0 30px;
    font-size: 18px;
    padding-top: 30px;
    padding-bottom: 20px;

    @media only screen and (max-width: 1023.98px) {
      font-size: 16px;
      padding-top: 20px;
    }

    @media only screen and (max-width: 575.98px) {
      font-size: 14px;
    }

    @media only screen and (max-width: 435.98px) {
      border-top: none;
      padding-top: 0;
    }

    @media only screen and (max-width: 399.98px) {
      margin: 0 15px;
    }

    @media only screen and (max-width: 399.98px) {
      margin: 0 15px;
    }

    @media only screen and (max-width: 300px) {
      font-size: 12px;
    }

    br {
      display: none;
      @media only screen and (max-width: 732.98px) {
        display: block;
      }
      @media only screen and (max-width: 435.98px) {
        display: block;
      }

    }

    p {
      line-height: 2em;
      margin: 0;
      font-size: 14px;
      @media only screen and (min-width: 576px) {
        font-size: 16px;
      }
      @media only screen and (min-width: 1024px) {
        font-size: 18px;
      }
    }

    div {
      span {
        line-height: 1.8em;
        font-size: 14px;
        @media (min-width: 576px) {
          font-size: 16px;
        }
        @media (min-width: 1024px) {
          font-size: 18px;
        }

        &:nth-child(3) {
          @media only screen and (max-width: 732.98px) {
            display: none;
          }
        }

        a {
          font-weight: 600;
          text-decoration: none;
          color: @grey-one;
          font-size: 14px;
          @media only screen and (min-width: 576px) {
            font-size: 16px;
          }
          @media only screen and (min-width: 1024px) {
            font-size: 18px;
          }
        }

        br {
          display: none;
          @media only screen and (max-width: 350px) {
            display: block;
          }
        }
      }
    }

    &_brown {
      background-color: @brown-one;
      border-top: none;
      color: @white;
      padding-top: 10px;
      padding-bottom: 20px;
      margin: 0;
      @media only screen and (max-width: 690px) {
        padding-left: 15px;
        padding-right: 15px;
      }

      div {
        span {
          a {
            color: @white;
          }
        }
      }
    }
  }

  &__add-file {
    fieldset {
      width: 100%;
      max-width: 651px;
      margin: auto;
      @media only screen and (max-width: 500px) {
        width: 100%;
        min-width: 270px;
      }
    }

    [id*="messages"] {
      padding-left: 0;
      .attach__name {
        text-align: center;
        font-size: 18px;
        font-weight: 600;
        @media only screen and (max-width: 575.98px) {
          font-size: 16px;
        }
      }
    }
  }
}

.task__title {
  display: block;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.8em;
  margin-bottom: 30px;
  transition: .3s ease-in-out;

  @media (max-width: 1023.98px) {
    font-size: 18px;
  }

  @media (max-width: 575.98px) {
    font-size: 16px;
  }

  @media (max-width: 300px) {
    font-size: 14px;
  }

  &_light {
    font-weight: 400;
  }
}

.task__title-main {
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  color: @color-green;
  line-height: 200%;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  margin: 0 0 20px;
  transition: .3s ease-in-out;

  @media only screen and (max-width: 1023.98px) {
    font-size: 20px;
  }

  @media only screen and (max-width: 760.98px) {
    flex-direction: column;
    padding-left: 10%;
    padding-right: 10%;
  }

  @media only screen and (max-width: 575.98px) {
    font-size: 18px;
  }

  @media only screen and (max-width: 300px) {
    font-size: 16px;
  }

  svg {
    margin-right: 15px;
    transition: .3s ease-in-out;

    @media only screen and (max-width: 1023.98px) {
      width: 40px;
      height: 40px;
    }
    @media only screen and (max-width: 760.98px) {
      width: 60px;
      height: 60px;
      margin-bottom: 20px;
    }
    @media only screen and (max-width: 479.98px) {
      width: 47px;
      height: 47px;
    }
  }
}

.task__text {
  display: block;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.8em;
  max-width: 615px;
  width: 100%;
  margin: 0 auto 36px;
  transition: .3s ease-in-out;

  @media only screen and (max-width: 1023.98px) {
    font-size: 16px;
  }

  @media only screen and (max-width: 575.98px) {
    font-size: 14px;
  }

  @media only screen and (max-width: 479.98px) {
    margin-bottom: 20px;
  }

  @media only screen and (max-width: 300px) {
    font-size: 12px;
  }

  &_green {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    color: @color-green;
    padding-top: 20px;
    margin-bottom: 15px;
    font-size: 16px;

    @media only screen and (max-width: 575.98px) {
      font-size: 14px;
    }

    @media only screen and (max-width: 479.98px) {
      margin-bottom: 20px;
    }

    @media only screen and (max-width: 300px) {
      font-size: 12px;
    }

    img {
      margin-right: 15px;
      @media only screen and (max-width: 360px) {
        width: 30px;
      }
    }
  }

  ul {
    padding: 0;
    margin-top: 30px;
    margin-bottom: 0;
    list-style-type: none;
    text-align: center;

    @media only screen and (max-width: 479.98px) {
      text-align: left;
    }

    li {
      line-height: 3em;
      @media only screen and (max-width: 479.98px) {
        line-height: 1.8em;
        padding-bottom: 10px;
      }
    }
  }

  &_mb-0 {
    margin-bottom: 0;
  }
}

.task__buttons {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  margin: 0 auto;
  transition: .3s ease-in-out;

  @media only screen and (max-width: 539.98px) {
    display: flex;
    flex-direction: column;
  }

  @media (min-width: @tablet-width) {
    form.task__add-file {
      width: 100%;
    }
  }

  .button {
    flex-grow: 1;
    margin: 0;
    width: auto;

    @media only screen and (max-width: 539.98px) {
      margin-left: auto;
      margin-right: auto;
    }
    @media only screen and (max-width: 399.98px) {
      max-width: 100%;
      min-width: 100%;
    }

    &:nth-child(2) {
      //&[role="button"] {
      //  margin-right: 30px;
      //  @media only screen and (max-width: 767.98px) {
      //    margin-right: 20px;
      //  }
      //  @media only screen and (max-width: 753.98px) {
      //    margin-bottom: 30px;
      //  }
      //  @media only screen and (max-width: 539.98px) {
      //    margin-right: auto;
      //  }
      //}

      //@media (min-width: @tablet-width) {
      //  margin-left: 0;
      //}
    }

    &:last-child {
      &[role="button"] {
        @media only screen and (max-width: 539.98px) {
          margin-left: auto;
          margin-right: auto;
        }
      }
    }

    &_bl-1 {
      border-left: 1px solid @grey-seven;
      margin-left: 30px;
      @media only screen and (max-width: 770.98px) {
        border-left: none;
        margin-left: 0;
      }

      .button_ml-30 {
        @media only screen and (max-width: 770.98px) {
          margin-left: 0;
        }
      }
    }

    &_ml-30 {
      margin-left: 30px;
      @media only screen and (max-width: 539.98px) {
        margin-left: 0;
      }
    }

    &_mr-30 {
      margin-right: 30px;
      @media only screen and (max-width: 539.98px) {
        margin-right: 0;
      }
    }

    &_mb-20 {
      @media only screen and (max-width: 539.98px) {
        margin-bottom: 20px;
      }
    }

    &_mb-0 {
      &:nth-child(2) {
        &[role="button"] {
          margin-bottom: 0;
        }
      }
    }

    &_last-child {
      margin-left: 30px;
      @media only screen and (max-width: 539.98px) {
        margin-top: 0;
      }
    }
  }

  &_pb-30 {
    padding-bottom: 30px;
    @media only screen and (max-width: 539.98px) {
      padding-bottom: 0;
    }
  }

  &_pb-41 {
    padding-bottom: 41px;
    @media only screen and (max-width: 435.98px) {
      padding-bottom: 30px;
    }
  }

  & form {
    width: 100%;
    @media (min-width: @tablet-width) {
      width: auto;
    }
  }

  [id*="control"] {
    display: flex;
    align-items: center;
    align-content: center;
    @media only screen and (max-width: 770.98px) {
      width: 100%;
      display: block;
    }
  }

  [id*="stepOne"] {
    display: flex;
    @media only screen and (max-width: 770.98px) {
      margin-top: 20px;
    }
    @media only screen and (max-width: 640px) {
      flex-direction: column;
      justify-content: center;
    }
    @media only screen and (max-width: 539.98px) {
      margin-top: 0;
    }

    a {
      position: relative;

      &:nth-child(1) {
        margin-left: 60px;
        @media only screen and (max-width: 770.98px) {
          margin-right: 30px;
          margin-left: 0;
        }
        @media only screen and (max-width: 640px) {
          margin-right: 0;
          margin-bottom: 30px;
        }
        @media only screen and (max-width: 539.98px) {
          margin-left: auto;
          margin-right: auto;
          margin-bottom: 0;
        }
      }

      &:nth-child(2) {
        @media only screen and (max-width: 770.98px) {
          margin-left: 0;
        }
        @media only screen and (max-width: 539.98px) {
          margin-top: 30px;
        }

        &::before {
          display: none;
        }
      }

      &:only-child {
        @media only screen and (max-width: 770.98px) {
          margin-right: 0;
        }
      }

      &[style*="display: none"] ~ &:nth-child(2) {
        &::before {
          display: block;
        }
      }

      &::before {
        content: "";
        width: 1px;
        height: 100%;
        background: @grey-seven;
        left: 0;
        position: absolute;
        display: block;
        top: 0;
        margin-left: -30px;
        @media only screen and (max-width: 770.98px) {
          display: none;
        }
      }
    }

    a[style*="display: none"] ~ span {
      margin-left: 60px;
      @media only screen and (max-width: 770.98px) {
        margin-left: 0;
      }
      @media only screen and (max-width: 539.98px) {
        margin-top: 0;
      }
      &::before {
        display: block;
        @media only screen and (max-width: 770.98px) {
          display: none;
        }
      }
    }

    a ~ span {
      @media only screen and (max-width: 539.98px) {
        margin-top: 30px;
      }
    }

    span {
      position: relative;
      margin-right: 0;
      margin-left: 30px;
      @media only screen and (max-width: 770.98px) {
        margin-left: 0;
      }

      &::before {
        content: "";
        width: 1px;
        height: 100%;
        background: @grey-seven;
        left: 0;
        position: absolute;
        display: none;
        top: 0;
        margin-left: -30px;
      }
    }

    .button__small {
      @media only screen and (max-width: 640px) {
        max-width: 320px;
        min-width: 320px;
      }
      @media only screen and (max-width: 399.98px) {
        max-width: 100%;
        min-width: 100%;
      }
    }
  }

  .button__little {
    @media only screen and (max-width: 640px) {
      max-width: 320px;
      min-width: 320px;
    }
    @media only screen and (max-width: 399.98px) {
      max-width: 100%;
      min-width: 100%;
    }
  }

  .button__label {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;

    &[style*="display: none"] ~ .button__label {
      .button-checkbox {
        border-left: none;
        border-right: none;
        margin-right: 0;
        margin-left: 0;
        padding-left: 0;
      }
    }

    &:first-child {
      .button {
        margin-right: 30px;
        @media only screen and (max-width: 539.98px) {
          margin-right: 0;
          margin-bottom: 20px;
        }
      }

      .button-checkbox {
        border-right: 0;
        margin-right: 0;
        border-left: none;
        padding-left: 0;
        padding-right: 0;
        @media only screen and (max-width: 539.98px) {
          margin-left: 0;
          margin-bottom: 0;
          padding-bottom: 25px;
        }
      }
    }

    &:nth-child(2) {
      @media only screen and (max-width: 539.98px) {
        margin-bottom: 0;
      }

      .button-checkbox {
        border-left: 1px solid @grey-seven;
        border-right: none;
        margin-right: 0;
        margin-left: 30px;
        padding-left: 30px;
        padding-right: 0;
        @media only screen and (max-width: 713.98px) {
          border-left: 1px solid @grey-seven;
        }
        @media only screen and (max-width: 539.98px) {
          margin-right: 0;
          margin-left: 0;
          margin-bottom: 0;
          border-left: none;
          padding-left: 0;
          padding-bottom: 25px;
        }
      }
    }

    &:nth-child(3) {
      width: auto;
      margin: 0 0 0 30px;
      padding-top: 0;
      font-weight: 400;
      border-right: 0;
      padding-right: 0;
      padding-left: 30px;
      border-left: 1px solid @grey-seven;
      @media only screen and (max-width: 770.98px) {
        border-left: none;
        padding-top: 20px;
        margin-top: 20px;
        padding-left: 0;
        width: 100%;
      }
      @media only screen and (max-width: 539.98px) {
        border-top: 1px solid @color-green;
        margin-top: 0;
        margin-left: 0;
      }

      .button-checkbox {
        border-right: 0;
        padding-right: 0;
        margin-right: 0;
        margin-left: 30px;
        padding-left: 30px;
        border-left: 1px solid @grey-seven;
        @media only screen and (max-width: 539.98px) {
          border-bottom: none;
          margin-bottom: 0;
          padding-bottom: 0;
          margin-left: 0;
        }
        @media only screen and (max-width: 479.98px) {
          margin-bottom: 0;
          padding-top: 25px;
          padding-bottom: 0;
        }
      }
    }
  }

  .button-checkbox {
    height: 100%;
    @media only screen and (min-width: 600px) {
      margin: 0 30px 0 0;
    }
    @media only screen and (max-width: 539.98px) {
      border: 0;
      padding-left: 0;
      padding-right: 0;
    }
  }
  .task__text[style*="display: none"] ~ .task__text {
    padding-left: 0;
    margin-left: 0;
    border-left: none;
  }
}

.task__archive-performed {
  text-align: center;
  padding-top: 55px;

  .task__title_light {
    margin-bottom: 13px;
  }

  button.acc__button {
    width: 60px;
    height: 60px;
    box-shadow: 0 1px 3px @black-rgba-one;
    margin: 0 auto 20px;
    padding: 0;
    transition: .3s ease-in-out;

    &:before {
      right: auto;
      left: 50%;
      transform: translate(-50%, -50%) rotate(225deg);
      margin-top: 5px;
      transition: .3s ease-in-out;
    }

    &:after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      border-radius: 10px;
      background: transparent;
      visibility: hidden;
      opacity: 0;
      transition: .2s ease-in-out;
      color: @white;
      font-size: 14px;
      font-weight: 400;
      padding: 5px 10px;
    }

    &:hover {
      box-shadow: 0 15px 30px @black-rgba-two;
      transform: translateY(-2px) scale(1.02);

      &::after {
        content: "";
        top: -10px;
        visibility: visible;
        opacity: 1;
        background: @black-rgba-three;
      }
    }

    &.active {
      &:before {
        transform: translate(-50%, -50%) rotate(45deg);
        margin-top: -2px;
      }

      &::after {
        content: "";
      }

      &:hover {
        &::after {
          content: "";
        }
      }
    }
  }

  .doc__block {
    background-color: transparent;
    padding: 0;
    display: block;
  }
}
