.modal {
    position: fixed;
    z-index: 999;
    display: none;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background: @black-rgba-three;

    &.active {
        display: block;
        animation: popupWrapperShow 0.4s cubic-bezier(.13, .3, .17, .99);
        animation-fill-mode: both;

        & .modal__box {
            animation: popupShow 0.4s cubic-bezier(.13, .3, .17, .99);
            animation-fill-mode: both;
        }
    }

    &.remove {
        animation: popupWrapperHide 0.4s cubic-bezier(.13, .3, .17, .99);
        animation-fill-mode: both;

        & .modal__box {
            animation: popupHide 0.4s cubic-bezier(.13, .3, .17, .99);
            animation-fill-mode: both;
        }
    }

    &__box {
        position: absolute;
        display: none;
        max-width: 500px;
        padding: 20px 45px;
        padding-bottom: 30px;
        background: #FFFFFF;
        box-shadow: 0 28px 59px @black-rgba-two,
                    0 40px 120px @black-rgba-five;
        border-radius: 25px;
        box-sizing: border-box;
        top: 50%;
        left: 50%;

        &.active {
            display: block;
        }

        &_p-none {
            padding: 0;
        }
    }

    &__text {
        margin: 0;
        margin-bottom: 22px;

        text-align: center;

        width: 100%;

        font-size: 22px;
        font-weight: 500;
    }

    &__buttons {
        width: 240px;

        margin: 0 auto;

        & .button {
            height: 53px;

            padding-top: 0;
            padding-bottom: 0;
            margin-bottom: 20px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__close {
        padding: 0;
        position: absolute;
        display: block;
        z-index: 2;
        width: 50px;
        height: 50px;
        top: -15px;
        right: -15px;
        border-radius: 50%;
        border: 2px solid @white;
        background-color: @grey-two;
        box-sizing: border-box;
        transition: 0.5s;
        cursor: pointer;
        transform: rotate(0deg);

        &:hover {
            transform: rotate(180deg);
            background-color: @color-red;
            &::before {
                transform: translate(-50%, -50%) rotate(135deg);
            }

            &::after {
                transform: translate(-50%, -50%) rotate(-135deg);
            }
        }

        &::before,
        &::after {
            content: "";
            position: absolute;
            display: block;
            width: 32px;
            height: 2px;
            top: 50%;
            left: 50%;
            background-color: @white;
            transition: .5s;
        }

        &::before {
            transform: translate(-50%, -50%) rotate(45deg);
        }

        &::after {
            transform: translate(-50%, -50%) rotate(-45deg);
        }

    }
}

@media (max-width: @tablet-width) {
    .modal {
        &__box {
            padding: 15px 20px;

            margin: 0 15px;
        }

        &__text {
            font-size: 16px;

            margin-bottom: 10px;
        }

        &__close {
            width: 40px;
            height: 40px;

            border-width: 2px;

            &::before,
            &::after {
                width: 25px;
                height: 3px;
            }
        }
    }
}