.help {

    & .button--back {
        margin-bottom: 22px;
    }

    & img {
        max-width: 100%;
        height: auto;
    }

    &--desktop {
        display: none;
    }

    &--mobile {
        & .main__block {
            &--tabs {
                padding: 0;
                background-color: transparent;
                @media only screen and (max-width: 767.98px) {
                    padding: 10px;
                }
            }
        }

        & .tab__button {
            position: relative;

            width: 100%;

            padding: 20px 30px;
            border-radius: 30px;
            margin-bottom: 15px;

            outline: none;

            &::before {
                display: none;
            }

            &.active {
                color: #858585;
                background: #fff;
            }
        }
        .acc__sub-list {
            padding-left: 0;
            .help__button {
                box-shadow: none;
            }
        }

    }
    &__faq {
        .help__acc-list .acc__button {
            @media only screen and (max-width: 767.98px) {
                color: #000;
                background: #d0e6e5;
                box-shadow: 0 0 10px @black-rgba-six;
                border-radius: 12px;
            }
        }
    }
}

.help__video-block {
    width: 100%;
    box-sizing: border-box;
    @media only screen and (max-width: 767.98px) {
        background-color: transparent;
        box-shadow: none;
        padding-right: 0;
        padding-left: 0;
    }
    @media only screen and (max-width: 359.98px) {
        padding-top: 40px;
    }
    .grid {
        @media only screen and (max-width: 767.98px) {
            justify-content: space-between;
        }
        @media only screen and (max-width: 599.98px) {
            justify-content: center;
        }
    }
    .grid__item {
        @media only screen and (max-width: 767.98px) {
            flex: 0 0 50%;
            max-width: 50%;
        }
        @media only screen and (max-width: 599.98px) {
            flex: 0 0 100%;
            max-width: 100%;
            padding: 0;
        }
    }
}

.help__video-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 315px;
    height: 220px;
    border-radius: 20px;
    overflow: hidden;
    margin-bottom: 15px;
    @media only screen and (max-width: 767.98px) {
        height: auto;
    }
    @media only screen and (max-width: 599.98px) {
        max-width: 100%;
    }
    img {
        @media only screen and (max-width: 599.98px) {
            width: 100%;
        }
    }
}

.help__search {
    display: none;

    border: none;

    padding: 13px 17px;
    padding-right: 40px;

    width: 255px;

    background-color: #fff;
    border-radius: 10px;

    outline: none;

    margin-bottom: 22px;

    background-image: url("../images/icons/search.svg");
    background-repeat: no-repeat;
    background-size: 17px;
    background-position: 95% center;

    box-sizing: border-box;

    &:focus {
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
    }
}

.help__acc-list .acc__button,
.doc__list .acc__button,
.help__button {
    position: relative;

    padding: 0;
    border: none;
    background-color: #fff;

    display: block;

    width: 100%;

    color: #000;
    font-family: inherit;
    font-size: 16px;
    text-align: center;
    font-weight: 500;
    text-decoration: none;

    padding: 20px 10px;
    padding-right: 45px;
    margin-bottom: 20px;

    border-radius: 30px;
    box-sizing: border-box;

    cursor: pointer;

    transition: box-shadow 0.3s, color 0.3s;

    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0);

    outline: none;

    &::before {
        content: "";
        position: absolute;
        display: block;

        width: 16px;
        height: 16px;

        right: 24px;
        top: 50%;
        transform: translateY(-50%) rotate(-45deg);

        border: 1px solid #000;
        border-left: none;
        border-top: none;

        transition: transform 0.3s;
    }

    &:hover,
    &.active {
        color: @color-green-text;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
    }

    &.active {
        &::before {
            transform: translateY(-70%) rotate(45deg);
        }
    }
}

.help__sub-item {
    display: block;
    background-color: #fff;
    width: 100%;
    border-radius: 30px;
    box-sizing: border-box;
    box-shadow: 0 0 10px 0 @black-rgba-six;
    overflow: hidden;
    margin-bottom: 10px;
    @media only screen and (max-width: 767.98px) {
        text-align: left;
        line-height: 1.5em;
    }
}

.help__text {
    padding: 25px 15px;
    color: #000;
}

.help__video-link {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    // transition: transform 0.2s;

    &::before {
        content: "";
        position: absolute;
        display: block;

        top: 0;
        left: 0;

        width: 100%;
        height: 100%;

        background-color: rgba(0, 0, 0, 0.5);

        background-image: url("../images/icons/play-button.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 55px;

        transition: background 0.2s;
    }

    &:hover,
    &:focus {
        // transform: scale(1.2);

        &:before {
            background-color: rgba(0, 0, 0, 0.3);
            background-image: url("../images/icons/play-button-green.svg");
        }
    }
}

.help__video {
    display: block;
}

.help__video-text {
    display: block;
    text-align: center;
    color: 3000;
    font-weight: 500;
}

.help__instructions {
    padding: 20px;

    border-radius: 20px;
    background-color: #fff;

    margin-bottom: 25px;

    &.hide {
        display: none;
    }
}

.help__button {
    padding: 10px 0;
    margin-bottom: 5px;

    text-align: left;

    border-radius: 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);

    &:last-child {
        margin-bottom: 0;
    }

    &::before {
        display: none;
    }
}

.help__title {
    display: block;

    font-size: 20px;
    text-align: left;
    font-weight: 500;

    margin-bottom: 20px;
    padding-left: 15px;
}

.help__text {
    margin: 0;

    margin-bottom: 15px;
}

.help__acc-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.help__acc-list .acc__sub-list {
    margin: 0;
    padding: 0;
    list-style: none;
}


@media (min-width: @tablet-width) {
    .help {
        &--desktop {
            display: block;
        }

        &--mobile {
            display: none;
        }
    }

    .help__search {
        display: block;
    }

    .help__instructions {
        padding: 0;
        background-color: transparent;
    }

    .help__button {
        padding: 20px 10px;
        border-radius: 30px;
        border: none;
        text-align: center;

        margin-bottom: 15px;
    }
}

.help__question {
    background-color: transparent;
    padding: 0;
    text-align: unset;
    @media only screen and (max-width: 767.98px) {
        padding-top: 20px;
        background-color: @white;
    }
}

.help__text {
    padding: 0;
}

.help__question-block {
    background-color: #fff;
    border-radius: 30px;
    padding: 20px 15px;
}

.help__sub-text {
    margin: 0;
    padding: 20px 10px;
    @media only screen and (max-width: 767.98px) {
        padding-right: 25px;
        padding-left: 25px;
    }
}

@media (min-width: @desktop-width) {
    .help__button {
        padding-left: 45px;
    }

    .help__question {
        padding: 20px 70px;
    }

    .help__question {
        background-color: #fff;
    }

    .help__title {
        padding-left: 0;
    }

    .help__sub-text {
        padding: 20px 30px;
    }
}