.number-task {
  padding: 17px 15px;
  color: @color-ash;
  font-size: 18px;
  font-weight: 500;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  position: relative;
  transition: .3s ease-in-out;
  img {
    margin-right: 9px;
  }
  @media only screen and (max-width: 479.98px) {
    font-size: 16px;
  }
}