.form-modal {
  width: 100%;
  position: relative;

  &__row {
    button {
      margin: auto;
      display: block;
    }
    &_center {
      text-align: center;
    }
    &_relative {
      position: relative;
    }
    &_pb-10 {
      padding-bottom: 10px;
    }
    &_pb-30 {
      padding-bottom: 30px;
      @media (max-width: @tablet-width) {
        padding-bottom: 20px;
      }
      @media only screen and (max-width: 450.98px) {
        padding-bottom: 5px;
      }
      @media only screen and (max-width: 380px) {
        padding-bottom: 0;
      }
    }
  }

  fieldset {
    padding: 0;
  }

  legend {
    font-weight: 500;
    font-size: 18px;
    line-height: 50px;
    display: block;
    transition: .3s ease-in-out;
    @media only screen and (max-width: 991.98px) {
      font-size: 16px;
    }
    @media only screen and (max-width: 900px) {
      font-size: 14px;
    }
    @media only screen and (max-width: 450.98px) {
      line-height: 1.5em;
    }
  }

  label {
    font-weight: 500;
    font-size: 18px;
    line-height: 50px;
    display: block;
    transition: .3s ease-in-out;
    @media only screen and (max-width: 991.98px) {
      font-size: 16px;
    }
    @media only screen and (max-width: 900px) {
      font-size: 14px;
    }
    @media only screen and (max-width: 380px) {
      line-height: 30px;
    }
  }

  textarea {
    background-color: @grey-three;
    border-radius: 10px;
    width: 100%;
    padding: 12px 32px;
    font-size: 18px;
    font-family: @font-montserrat;
    font-weight: 400;
    border: 1px solid transparent;
    transition: .2s;
    margin-bottom: 40px;

    @media only screen and (max-width: 479.98px) {
      padding: 12px 10px;
    }

    @media only screen and (max-width: 450.98px) {
      margin-bottom: 20px;
    }

    @media only screen and (max-width: 380px) {
      font-size: 14px;
    }

    &:focus {
      outline: 0;
      border: 1px solid @green-rgba-one;
    }
  }

  &__text {
    &_red {
      color: @color-red;
    }
  }

  &__tooltip {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    width: 100%;
    color: @white;
    font-family: @font-montserrat;
    font-weight: 500;
    font-size: 14px;
    border-radius: 10px;
    box-shadow: 0 3px 10px @black-rgba-five;
    transition: .5s;
    padding: 5px 10px;
    line-height: 1.5em;
    bottom: 10px;
    z-index: 10;
    background-color: @red-rgba-three;
    @media only screen and (max-width: 450.98px) {
      font-size: 12px;
    }
  }

}

.rating-area {
  overflow: hidden;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  flex-direction: row-reverse;
  padding-left: 30px;
  padding-right: 30px;
  transition: .3s ease-in-out;

  @media only screen and (max-width: 991.98px) {
    padding-left: 0;
    padding-right: 0;
  }

  &:not(:checked) {
    > input {
      display: none;
    }

    > label {
      width: 100%;
      min-width: 60px;
      height: 100%;
      min-height: 60px;
      padding: 0;
      cursor: pointer;
      color: @grey-two;
      text-align: center;
      vertical-align: middle;
      position: relative;
      transition: color .2s ease-in-out;
      opacity: 1;
      animation-fill-mode: both;
      animation-duration: .7s;
      @media only screen and (max-width: 380px) {
        min-width: 50px;
        max-width: 50px;
        min-height: 50px;
        max-height: 50px;
      }

      &:before {
        content: '';
        position: relative;
        width: 100%;
        min-width: 60px;
        height: 100%;
        min-height: 60px;
        transition: .2s ease-in-out;
        font-size: 70px;
        @media only screen and (max-width: 380px) {
          font-size: 35px;
          min-width: 50px;
          max-width: 50px;
          min-height: 50px;
          max-height: 50px;
        }
      }

      &:hover {
        color: @gold-one;
        &:before {
          @media only screen and (min-width: 400px) {
            transform: scale(1.1);
          }
        }
      }

      &:hover ~ label {
        color: @gold-one;
        &:before {
          @media only screen and (min-width: 400px) {
            transform: scale(1.1);
          }
        }
      }
      &.delay {
        &_005 {
          animation-delay: .05s;
        }
        &_01 {
          animation-delay: .1s;
        }
        &_02 {
          animation-delay: .2s;
        }

        &_03 {
          animation-delay: .3s;
        }

        &_04 {
          animation-delay: .4s;
        }
      }
    }
  }

  > input {
    &:checked {
      ~ label {
        color: @gold-one;
        &:before {
          @media only screen and (min-width: 400px) {
            transform: scale(1.1);
          }
        }

        &:hover {
          color: @gold-one;
          &:before {
            @media only screen and (min-width: 400px) {
              transform: scale(1.1);
            }
          }

          ~ label {
            color: @gold-one;
            &:before {
              @media only screen and (min-width: 400px) {
                transform: scale(1.1);
              }
            }
          }
        }
      }

      + label {
        &:hover {
          color: @gold-one;
          &:before {
            @media only screen and (min-width: 400px) {
              transform: scale(1.1);
            }
          }

          ~ label {
            color: @gold-one;
            &:before {
              @media only screen and (min-width: 400px) {
                transform: scale(1.1);
              }
            }
          }
        }
      }
    }
  }

  > label {
    &:hover {
      ~ input {
        &:checked {
          ~ label {
            color: @gold-one;
            &:before {
              @media only screen and (min-width: 400px) {
                transform: scale(1.1);
              }
            }
          }
        }
      }
    }
  }
}

.rate-area {
  > label {
    &:active {
      position: relative;
    }
  }
}

@keyframes ratingAreaShow {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: translate3d(-100%,0,0);
  }

  100% {
    opacity: 1;
    visibility: visible;
    transform: translateZ(0);
  }
}

@keyframes ratingAreaHide {
  0% {
    opacity: 1;
    visibility: visible;
    transform: translateZ(0);
  }

  100% {
    opacity: 0;
    visibility: hidden;
    transform: translate3d(-100%,0,0);
  }
}