.pay {
    background-color: transparent;
    padding: 0;
}

.pay__title {
    display: block;

    font-size: 22px;
    font-weight: 500;

    margin-bottom: 10px;

    background-color: #fff;
    border-radius: 20px;

    padding: 10px 0;
}

.pay__table-wrapper {
    background-color: #fff;
    border-radius: 20px;
    padding: 0 10px;
}

.pay__table {
    border-collapse: collapse;

    width: 100%;

    font-size: 14px;
    font-weight: 500;

    box-sizing: border-box;
}

.pay__tr {
    color: #353535;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    & .button {
        color: #353535;
        border-color: #353535;

        min-width: 108px;
        padding: 4px 10px;

        &:hover,
        &:focus,
        &:active {
            color: #353535;
            border-color: #353535;
            background: #fff;
        }
    }

    &--green {
        color: @color-green-light;

        & .button {
            color: #fff;
            border-color: @color-green;
            background-image: @gradient-green;

            &:hover,
            &:focus,
            &:active {
                opacity: 0.9;
                color: #fff;
                border-color: @color-green;
                background-image: @gradient-green;
            }
        }
    }

    &--red {
        color: @color-red;

        & .button {
            color: #fff;
            border-color: @color-red;
            background-image: @gradient-red;

            &:hover,
            &:focus,
            &:active {
                opacity: 0.9;
                color: #fff;
                border-color: @color-red;
                background-image: @gradient-red;
            }
        }
    }
}

.pay__td {
    padding: 10px 0;

    width: auto;
    text-align: center;

    box-sizing: border-box;

    &:first-child {
        display: none;
    }

    &:nth-child(2) {
        text-align: left;
    }

    &:last-child {
        text-align: center;
        width: 100%;

        border-bottom: 1px solid #eeeeee;
    }
}

.pay__text {
    display: block;

    color: #555555;
    font-size: 20px;
    font-weight: 500;

    margin-bottom: 30px;
}

@media (min-width: @desktop-width) {
    .pay {
        background-color: #fff;
        padding: 20px 10px;
    }

    .pay__title {
        font-size: 24px;
        margin-bottom: 45px;
    }

    .pay__table-wrapper {
        padding: 0;
    }

    .pay__table {
        font-size: 20px;
    }

    .pay__tr {
        display: table-row;

        & .button {
            min-width: 240px;
            padding: 16px 20px;
        }
    }

    .pay__td {
        width: 25%;
        padding: 40px 35px;

        border-top: 1px solid #eeeeee;

        &:first-child {
            display: table-cell;
        }

        &:nth-child(2) {
            text-align: center;
        }

        &:last-child {
            text-align: right;
            width: auto;

            border-top: 1px solid #eeeeee;
        }
    }
}