.tabs {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  @media only screen and (max-width: 1149.98px) {
    justify-content: flex-start;
  }

  & .doc__block {
    box-shadow: 0 0 10px @black-rgba-six;
    border-radius: 12px;
    margin-bottom: 20px;
  }
}

.tab {
  &__button {
    border: none;
    background: @white;
    padding: 32px 44px;
    color: @grey-five;
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;
    transition: .2s;
    position: relative;
    z-index: 2;
    overflow: hidden;
    @media only screen and (max-width: 1301.98px) {
      display: flex;
      position: relative;
      width: 100%;
      padding-right: 15px;
      padding-left: 15px;
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;
      justify-content: center;
    }
    @media only screen and (max-width: 1149.98px) {
      border-radius: 50px;
      box-shadow: 0 0 10px @black-rgba-six;
      flex: 0 0 47%;
      max-width: 47%;
      margin-bottom: 30px;
    }
    @media only screen and (max-width: 767.98px) {
      font-size: 18px;
      flex: 0 0 100%;
      max-width: 100%;
      margin-bottom: 0;
    }

    &:nth-child(2n) {
      @media only screen and (max-width: 1149.98px) {
        margin-left: 15px;
      }
      @media only screen and (max-width: 767.98px) {
        margin-left: 0;
      }
    }

    &:nth-child(2n+1) {
      @media only screen and (max-width: 1149.98px) {
        margin-right: 15px;
      }
      @media only screen and (max-width: 767.98px) {
        margin-right: 0;
      }
    }

    &::before {
      content: '';
      display: block;
      background: @gradient-green;
      position: absolute;
      z-index: -1;
      bottom: -100%;
      left: 0;
      width: 100%;
      height: 100%;
      transition: .3s;
    }

    &::after {
      position: absolute;
      width: 18px;
      height: 18px;
      right: 24px;
      top: 50%;
      transform: translateY(-50%) rotate(-45deg);
      border: 1px solid @black-one;
      border-left: none;
      border-top: none;
      transition: transform .35s;
      @media only screen and (max-width: 1149.98px) {
        content: "";
        display: block;
      }
    }

    &:hover {
      @media only screen and (min-width: 768px) {
        color: @white;
      }

      &::before {
        bottom: 0;
      }
    }

    &.active {
      color: @white;
      cursor: default;

      &::before {
        bottom: 0;
      }

      &::after {
        @media only screen and (max-width: 1149.98px) {
          transform: translateY(-70%) rotate(45deg);
          border: 1px solid @white;
          border-left: none;
          border-top: none;
        }
        @media only screen and (max-width: 767.98px) {
          border: 1px solid @black-two;
          border-left: none;
          border-top: none;
        }
      }
    }

    &.show {
      color: @white;
      cursor: default;

      &::before {
        bottom: 0;
      }
    }
  }
}

.tab__wrapper {
  width: 100%;
}

.tab__item {
  display: none;

  &.active {
    display: block;
  }
}
