.login {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    flex-grow: 1;

    color: #fff;

    background-color: #383838;
    background-image: url("../images/background/bg-mobile.jpg");
    background-size: cover;
    background-position: center;

    &::before {
        content: "";
        position: absolute;
        display: block;
        width: 100vw;
        min-height: 100vh;

        background-color: rgba(43, 43, 43, 0.8);
    }
}

.login__content {
    position: relative;
    z-index: 1;

    max-width: 680px;
    width: 100%;

    padding: 0 10px;
}

.login__title {
    margin: 0;

    font-size: 24px;
    text-align: center;

    margin-bottom: 55px;
}

.login__input {
    border: none;

    width: 100%;

    color: #fff;
    font-size: 16px;
    font-weight: 700;

    padding: 20px 25px;

    background-color: rgba(0,0,0,0);
    border-bottom: 2px solid #666666;

    margin-bottom: 40px;

    outline: none;
    box-sizing: border-box;

    transition: border 0.2s;

    &:focus {
        border-bottom: 2px solid #fff;
    }
}

.login__link {
    display: block;

    color: #fff;
    font-size: 17px;
    font-weight: 500;

    padding-left: 15px;
    margin-bottom: 60px;

    transition: color 0.2s;

    &:hover,
    &:focus {
        color: lighten(@color-green, 25%);
    }
}

.login__button {
    border: none;

    width: 100%;

    color: #fff;
    font-size: 19px;

    padding: 26px 10px;

    border-radius: 20px;
    background-color: @color-green;

    transition: background 0.3s;

    cursor: pointer;

    &:hover,
    &:focus,
    &:active {
        background-color: lighten(@color-green, 5%);
    }
}

@media (min-width: @desktop-width) {
    .login {
        background-image: url("../images/background/bg-login.jpg");
    }
}