.doc {
  & .acc__item {
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, .1);
    border-radius: 30px;
    margin-bottom: 10px;
    background-color: #fff;
  }
}

.doc__block {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;

  border-radius: 30px;
  background-color: #fff;

  padding: 10px 25px;
  // margin: 0 -10px;
  // margin-bottom: -30px;
  margin-bottom: 10px;
}

.doc__list {
  margin: 0;
  padding: 0;
  list-style: none;

  & .acc__sub-list {
    margin: 0;
    padding: 0;
    list-style: none;
  }
}

.doc__item {

  @media only screen and (max-width: 767.98px) {
    text-align: left;
    line-height: 1.5em;
  }

  &:hover,
  &.active {
    &::before {
      color: @color-green-text;
    }
  }

  &--help {
    & .acc__button {
      color: #000;

      background: #D0E6E5;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
      border-radius: 12px;
    }

    &-white {
      & .acc__button {
        background-color: #fff;
      }
    }
  }
}

.doc__link {
  position: relative;

  display: flex;
  align-items: center;
  justify-content: flex-start;

  text-align: center;
  width: 250px;
  height: 28px;

  margin: 0 10px;
  margin-bottom: 30px;

  &:hover::after {
    content: attr(data-title);
    position: absolute;

    left: 35px;
    top: -20px;
    z-index: 1;

    color: #696969;
    font-size: 11px;
    font-weight: 500;
    padding: 5px 10px;
  }
}

.link__text {
  margin: 0;

  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

@media (min-width: @tablet-width) {
  .doc__block {
    padding: 20px 55px;
  }
}