.toggle-modal {
  position: fixed;
  z-index: 999;
  display: none;
  width: 100%;
  max-width: 500px;
  top: 50%;
  left: 50%;
  animation-duration: .7s;
  transition: .3s ease-in-out;

  @media (max-width: 530px) {
    padding: 0 15px;
  }

  &.active {
    display: block;
    animation: popupShow 0.4s cubic-bezier(.13, .3, .17, .99);
    animation-fill-mode: both;

    .rating-area:not(:checked) {
      > label {
        animation-name: ratingAreaShow;
        animation-duration: .7s;
        animation-timing-function: cubic-bezier(.13, .3, .17, .99);
      }
    }

  }

  &.remove {
    animation: popupHide 0.4s cubic-bezier(.13, .3, .17, .99);

    .rating-area:not(:checked) {
      > label {
        animation-name: ratingAreaHide;
        animation-duration: .7s;
        animation-timing-function: cubic-bezier(.13, .3, .17, .99);
      }
    }
  }

  &__box {
    position: relative;
    z-index: 1;
    padding: 20px 45px 30px;
    box-shadow: 0 28px 59px @black-rgba-two,
                0 40px 120px @black-rgba-five;
    border-radius: 25px;
    overflow: hidden;
    height: 100%;

    &_p-none {
      padding: 0;
    }
  }

  &__text {
    margin: 0 0 22px;
    text-align: center;
    width: 100%;
    font-size: 22px;
    font-weight: 500;

    @media (max-width: @tablet-width) {
      font-size: 16px;
      margin-bottom: 10px;
    }
  }

  &__buttons {
    width: 240px;
    margin: 0 auto;

    & .button {
      height: 53px;
      padding-top: 0;
      padding-bottom: 0;
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__close {
    padding: 0;
    position: absolute;
    display: block;
    z-index: 2;
    width: 50px;
    height: 50px;
    top: -15px;
    right: -15px;
    border-radius: 50%;
    border: 2px solid @white;
    background-color: @grey-two;
    box-sizing: border-box;
    transition: 0.5s;
    cursor: pointer;
    transform: rotate(0deg);

    &:hover {
      transform: rotate(180deg);
      background-color: @color-red;

      &::before {
        transform: translate(-50%, -50%) rotate(135deg);
      }

      &::after {
        transform: translate(-50%, -50%) rotate(-135deg);
      }
    }

    &:focus {
      background-color: @color-red;
    }

    @media (max-width: @tablet-width) {
      width: 40px;
      height: 40px;
      border-width: 2px;
    }
    @media only screen and (max-width: 530px) {
      right: 10px;
    }

    @media only screen and (max-width: 450.98px) {
      top: -10px;
      right: 10px;
    }

    @media only screen and (max-width: 380px) {
      top: 5px;
      right: 20px;
      border: none;
      background-color: transparent;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      display: block;
      width: 32px;
      height: 2px;
      top: 50%;
      left: 50%;
      background-color: @white;
      transition: .5s;

      @media (max-width: @tablet-width) {
        width: 25px;
        height: 2px;
      }
    }

    &::before {
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }

  }

  &__header {
    background-color: @color-green;
    text-align: center;
    padding: 21px 30px 21px;

    @media only screen and (max-width: 450.98px) {
      padding-top: 15px;
      padding-bottom: 15px;
    }

    @media only screen and (max-width: 400px) {
      padding-left: 20px;
      padding-right: 20px;
    }

    figure {
      margin: 0;

      img {
        margin-bottom: 14px;
        transition: .3s ease-in-out;
        @media (max-width: @tablet-width) {
          width: 43px;
        }
        @media only screen and (max-width: 450.98px) {
          width: 35px;
          margin-bottom: 5px;
        }
        @media only screen and (max-width: 380px) {
          width: 25px;
        }
      }

      figcaption {
        font-weight: 500;
        font-size: 22px;
        line-height: 27px;
        color: @white;
        transition: .3s ease-in-out;
        @media only screen and (max-width: 991.98px) {
          font-size: 18px;
        }
        @media only screen and (max-width: 900px) {
          font-size: 16px;
        }
      }
    }
  }

  &__body {
    padding: 30px 30px 30px;

    @media only screen and (max-width: 450.98px) {
      padding: 20px 20px 20px;
    }

    @media only screen and (max-width: 400px) {
      padding-left: 20px;
      padding-right: 20px;
    }

    &_white {
      background-color: @white;
      @media only screen and (max-width: 380px) {
        padding-top: 10px;
      }
    }

    &_pb-50 {
      padding-bottom: 50px;
      @media only screen and (max-width: 450.98px) {
        padding-bottom: 30px;
      }
      @media only screen and (max-width: 380px) {
        padding-bottom: 20px;
      }
    }
  }
  &__link {
    color: @color-green;
    text-decoration: underline;
    transition: .2s;
    cursor: pointer;
    &:hover {
      text-decoration: none;
    }
  }
  &__successfully {
    p {
      &:first-child {
        margin-bottom: 0;
      }
    }
    .button {
      margin: auto;
    }
  }
}

.toggle-modal-bg {
  position: fixed;
  z-index: 998;
  display: none;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: @black-rgba-three;
  overflow: hidden;

  &.active {
    display: flex;
    animation: popupWrapperShow 0.4s cubic-bezier(.13, .3, .17, .99);
  }

  &.remove {
    animation: popupWrapperHide 0.4s cubic-bezier(.13, .3, .17, .99);
  }
}
