.nav__list {
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.nav__item {
    width: 100%;
}

.nav__link {
    display: block;

    color: @color-ash;
    font-size: 18px;
    font-weight: 500;
    text-decoration: none;
    text-align: center;

    padding: 15px 20px;

    transition: color 0.2s;

    &:hover,
    &:focus {
        color: @color-green;
    }

    &.active {
        color: #fff;
        background-image: @gradient-green;

        cursor: default;
    }
}

@media (min-width: 1212px) {
    .nav__link {
        padding: 30px 20px;
    }
    .nav__item {
        width: auto;
        flex-grow: 1;
    }
}
  