.main {
    flex-grow: 1;
    width: 100%;
    max-width: 1400px;
    padding: 40px 10px 0;
    margin: 62px auto 100px;
    box-sizing: border-box;

    @media (min-width: 0) {
        margin-top: 70px;
        padding-top: 0;
    }

    &--samll {
        max-width: 1180px;
    }

    & .info {
        margin-bottom: 40px;
    }
    &__text {
        @media only screen and (max-width: 767.98px) {
            text-align: left;
            line-height: 1.5em;
            img {
                display: block;
                margin: 0 auto 40px;
            }
        }
    }
    &_pt-0 {
        @media only screen and (min-width: 280px) {
            padding-top: 0;
        }
    }
}

.main__block {
    border-radius: 25px;
    background-color: #fff;
    box-shadow: 0 1px 3px @black-rgba-one;
    transition: .3s ease-in-out;
    position: relative;
    overflow: hidden;
    text-align: center;
    margin-bottom: 22px;
    padding: 50px 15px 30px;

    @media only screen and (min-width: 360px) {
        padding: 40px 15px;
    }

    @media only screen and (min-width: 576px) {
        padding: 40px 30px;
    }

    @media only screen and (min-width: 680px) {
        padding: 40px 50px;
    }

    @media only screen and (min-width: 718px) {
        padding: 40px 30px;
    }

    @media only screen and (min-width: 760px) {
        padding: 72px 30px 40px;
    }

    @media only screen and (min-width: 1200px) {
        padding: 36px 50px 40px;
    }

    &--tabs {
        padding: 0 10px;
        @media only screen and (max-width: 1149.98px) {
            background-color: transparent;
            border-radius: 0;
            box-shadow: none;
            padding-top: 10px;
            padding-bottom: 10px;
        }
        @media only screen and (min-width: 1200px) {
            padding: 0 20px;
        }

    }
    &.attention {
        border: 2px solid @color-red;
        .task-icon {
            background: @color-red;
            border-radius: 0 22px 0 25px;
        }
        .task__title-main {
            color: @color-red;
            svg {
                path {
                    fill: @color-red;
                }
            }
        }
        &_uppercase {
            border: 2px solid @color-red;
            .task-icon {
                background: @color-red;
                border-radius: 0 22px 0 25px;
            }
            .task__title-main {
                text-transform: uppercase;
                color: @color-red;
                svg {
                    path {
                        fill: @color-red;
                    }
                }
            }
        }
    }
    &.performed {
        .task-icon {
            background: @grey-one;
        }
    }
    &_footer-message {
        padding: 36px 0 0;
        .task-icon {
            background-color: @brown-one;
        }
    }
}

