.cause {
    & .acc__button {
        text-align: left;

        padding-left: 40px;
    }

    & .doc__list {
        counter-reset: myCounter;
    }

    & .doc__item {
        position: relative;

        &::before {
            counter-increment: myCounter;
            content: counter(myCounter) '.';

            position: absolute;
            z-index: 2;

            font-weight: 500;

            top: 20px;
            left: 23px;

            transition: color 0.2s;
        }
    }
}

.cause__list {
    padding: 0;
    margin: 0;

    padding-left: 25px;

    text-align: left;
}

.cause__item {
    position: relative;

    font-weight: 500;
    margin-bottom: 30px;

    &--green {
        color: @color-green-text;

        & .acc__button {
            color: @color-green-text;
        }
    }

    &--gray {
        color: @grey-four;

        & .acc__button {
            color: @grey-four;
        }
    }

    &--arrow {
        color: #000;

        &::before {
            content: "" !important;

            width: 22px;
            height: 18px;

            left: 15px !important;

            background-image: url("../images/icons/right-arrow.svg");
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;

        }

        & .acc__button {
            color: @color-green-text;
        }
    }
}

@media (min-width: @tablet-width) {
    .cause__list {
        padding-left: 75px;
    }

    .cause {
        & .acc__button {
            padding-left: 75px;
        }

        & .doc__item {
            position: relative;

            &::before {
                left: 58px;
            }
        }
    }

    .cause__item {
        &--green {

            &::before {
                left: 50px !important;
            }
        }
    }
}