.footer {
  width: 100%;
  min-height: 34px;
  background-color: @white;
  @media only screen and (max-width: 399.98px) {
    padding-left: 15px;
    padding-right: 15px;
  }

  .toggle-modal__link {
    @media only screen and (max-width: 399.98px) {
      width: 100%;
      display: block;
      line-height: 50px;
    }
  }
}

.footer__text {
  display: block;
  width: 100%;

  color: #9db1bc;
  text-align: center;
  font-size: 15px;
  font-weight: 500;

  padding: 10px 0;
}

@media (min-width: @tablet-width) {
  .footer {
    min-height: 74px;
  }

  .footer__text {
    padding: 25px 0;
  }
}