.main-page {
  flex-grow: 1;
  width: 100%;
  max-width: 1420px;
  padding: 0 10px;
  margin: 0 auto 0;
  box-sizing: border-box;

  &__row {
    padding-right: 15px;
    padding-left: 15px;
    display: flex;
    flex-wrap: wrap;
  }

  &__cards {
    width: 100%;
    display: flex;
    justify-content: space-between;

    &-item,
    &-item--smaller {
      width: 820px;
      display: flex;
      flex-direction: column;

      &:last-child {
        width: 540px;
      }
    }

    &-item--smaller {
      margin-top: 25px;
      margin-left: 40px;
    }

    &-step,
    &-step--fixed-size {
      width: calc(100% - 40px);
      padding: 10px 20px;
      position: relative;
      margin-bottom: 40px;
      background: #FFFFFF;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
      border-radius: 12px;
    }

    &-step--fixed-size {
      max-height: 800px;
      min-width: 400px;
      min-height: 304px;
    }

    &-makecall {
      width: 240px;
      height: 53px;
      margin: -53px auto 25px;
    }

    &-header,
    &-header--left-align,
    &-name,
    &-name--smallmargin,
    &-time {
      width: 100%;

      margin: 19px 0 0;
      text-align: center;

      line-height: 22px;
      font-size: 18px;
      font-family: 'Montserrat', sans-serif;
      font-style: normal;
      font-weight: bold;
      letter-spacing: -.02em;
      color: #232222;
    }

    &-header--left-align {
      text-align: left;
    }

    &-name,
    &-name--smallmargin {
      margin: 0 0 29px;

      br {
        display: none;
      }
    }

    &-time {
      width: calc(100% - 10px);
      color: #888;
      padding: 24px 0 9px;
      margin: 0 auto;
      font-weight: 500;
      position: relative;

      br {
        display: none;
      }
    }

    &-acontainer,
    &-acontainer--smallmargin {
      position: relative;
      width: 125px;
      height: 125px;
      border-radius: 50%;
      overflow: hidden;
      margin: 25px auto;
    }

    &-acontainer--smallmargin {
      margin: 20px auto;
    }

    &-avatar {
      position: absolute;

      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      width: 100%;
      display: block;
      object-fit: cover;
    }

    &-contacts {
      width: calc(100% - 10px);

      margin: 0 auto;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      font-family: 'Montserrat', sans-serif;
      font-style: normal;
      font-weight: 500;
      color: #888;
      position: relative;

      &-item {
        color: #888;
        line-height: 26px;
        font-size: 18px;
        margin: 20px 30px;
        text-decoration: none;

        &:hover {
          opacity: .5;
        }

        &:last-child {
          text-decoration: underline;
        }
      }

      &-image {
        float: left;
        margin-right: 7px;
      }
    }

    &-consultants {
      padding: 0;
      margin: 0;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;

    }

    &-consitem {
      display: block;
      width: 180px;
    }

    &-consnames,
    &-constimes {
      width: 100%;
      font-family: 'Montserrat', sans-serif;
      font-style: normal;
      font-weight: bold;
      font-size: 14px;
      line-height: 17px;
      text-align: center;
      letter-spacing: -0.02em;
      padding: 14px 0;
      margin: 0;
    }

    &-constimes {
      font-weight: 500;
      color: #888888;
      position: relative;
    }

    &-scroll-box {
      overflow: hidden;
      overflow-y: auto;
      max-height: 700px;
      margin: 22px 0;
    }

    &-table {
      width: 100%;
      font-family: 'Montserrat', sans-serif;
      font-style: normal;
      font-weight: 300;
      font-size: 18px;
      line-height: 22px;
      letter-spacing: -0.02em;

      &-row {
        height: 44px;

        td {
          &:nth-child(1) {
            color: #888888;
          }

          &:nth-child(2) {
            text-align: center;
            color: #888888;
          }

          &:nth-child(3) {
            font-weight: 600;
            text-align: right;
            letter-spacing: -0.02em;
            color: #25615E;
          }
        }
      }
    }
  }

  &__top-delimetr::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: #c4c4c4;
    display: block;
  }
}


@media (max-width: 779px) {
  .main-page {
    &__cards-makecall {
      margin-top: 0;
    }

    &__cards {
      display: block;

      &-item,
      &-item--smaller {
        width: 100%;
        flex-wrap: wrap;
        justify-content: space-between;

        &:last-child {
          width: 100%;

          p {
            text-align: center;
          }
        }
      }

      &-item--smaller {
        margin: 0;
      }

      &-step,
      &-step--fixed-size {
        width: calc(100% - 20px);
        padding: 10px;
        margin-bottom: 10px;
      }

      &-step--fixed-size {
        min-height: auto;
        min-width: auto;
      }

      &-header {
        margin: 7px 0 0;

        font-size: 14px;
        line-height: 17px;
      }

      &-acontainer,
      &-acontainer--smallmargin {
        width: 100px;
        height: 100px;
        margin: 13px 0 3px 7px;
        float: left;
      }

      &-acontainer--smallmargin {
        margin: 0 0 0 7px;
      }

      &-avatar {
        width: 100px;
      }

      &-name,
      &-name--smallmargin,
      &-time {
        width: calc(100% - 126px);

        font-size: 12px;
        line-height: 15px;
        text-align: left;

        margin: 27px 0 0 17px;
        float: left;
      }

      &-name--smallmargin {
        margin-top: 11px;
      }

      &-delimetr {
        display: none;
      }

      &-time {
        margin-top: 7px;

        br {
          display: block;
        }
      }

      &-consultants {
        display: block;
      }

      &-consitem {
        padding: 10px 0;
        float: left;
        width: 100%;
        border-bottom: 1px solid #c4c4c4;

        &:last-child {
          border: 0;
        }

      }

      &-consul-del {
        display: none;
      }

      &-consnames,
      &-constimes {
        width: calc(100% - 124px);
        float: left;
        margin: 17px 0 0 17px;
        text-align: left;
        font-size: 12px;
        line-height: 15px;
      }

      &-scroll-box {
        overflow: visible;
        max-height: unset;
      }

      &-contacts {
        width: calc(100% - 124px);
        float: left;
        margin: 8px 0 0 17px;

        &-item {
          width: 100%;
          font-size: 12px;
          line-height: 15px;
          margin: 0;
          text-align: left;
        }

        &-image {
          display: none;
        }
      }

      &-table {
        margin: 10px 0;
        font-size: 14px;
        line-height: 17px;

        &-row {
          height: 30px;
        }
      }
    }
  }
}

@media (max-width: 1211px) {
  .main-page {
    margin-top: 61.81px;
  }

}

@media (max-width: 1100px) {
  .main-page {
    &__cards {
      &-consultants {
        justify-content: space-evenly;
      }

      &-contacts {
        &-item {
          margin: 10px auto;
        }
      }
    }
  }
}

@media (max-width: 900px) {
  .main-page {
    margin-top: 0;

    &__cards-makecall {
      margin-top: 0;
    }

    &__cards {
      &-item {
        width: 100%;

        flex-wrap: wrap;
        justify-content: space-between;

        &:last-child {
          width: 100%;

          p {
            text-align: center;
          }
        }
      }

      &-table {
        margin: 0;
        font-size: 12px;
        line-height: 14px;

        &-row {
          height: 20px;
        }
      }

      &-step,
      &-step--fixed-size {
        width: calc(100% - 20px);
        padding: 10px;
        margin-bottom: 10px;
      }

      &-step--fixed-size {
        max-height: unset;
        min-width: auto;
      }

      &-header,
      &-header--left-align {

        margin: 7px 0 0;

        font-size: 14px;
        line-height: 17px;
      }

      &-acontainer,
      &-acontainer--smallmargin {
        width: 100px;
        height: 100px;
        margin: 13px 0 3px 7px;
        float: left;
      }

      &-acontainer--smallmargin {
        margin: 0 0 0 7px;
      }

      &-name,
      &-name--smallmargin,
      &-time {
        br {
          display: block;
        }

        float: left;
        width: calc(100% - 126px);
        font-size: 12px;
        line-height: 15px;

        margin: 27px 0 0 17px;
        text-align: left;
      }

      &-name--smallmargin {
        margin-top: 11px;
      }

      &-time {
        margin-top: 7px;
        padding: 0;

        br {
          display: block;
        }
      }

      &-consultants {
        display: block;
      }

      &-consitem {
        padding: 10px 0;
        float: left;
        width: 100%;
        border-bottom: 1px solid #c4c4c4;

        &:last-child {
          border: 0;
        }

      }

      &-consnames,
      &-constimes {
        width: calc(100% - 124px);
        float: left;
        margin: 17px 0 0 17px;
        padding: 0;
        text-align: left;
        font-size: 12px;
        line-height: 15px;
      }

      &-constimes {
        margin: 7px 0 0 17px;

      }

      &-contacts {
        width: calc(100% - 124px);
        float: left;
        margin: 8px 0 0 17px;

        &-item {
          width: 100%;
          font-size: 12px;
          line-height: 15px;
          margin: 0 auto;
          text-align: left;
          text-decoration: none;

          &:last-child {
            text-decoration: none;
          }
        }

        &-image {
          display: none;
        }
      }

      &-scroll-box {
        margin: 6px 0;
      }
    }

    &__top-delimetr::before {
      display: none;
    }
  }
}