.form {
  max-width: 660px;
  width: 100%;

  padding: 15px 10px;
  margin: 0 auto;

  box-sizing: border-box;

  margin-bottom: 45px;

  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  border-radius: 20px;

  &--mobile-hiden {
    display: none;
  }
}

.form__title {
  display: block;

  font-size: 24px;
  font-weight: 500;
  text-align: center;

  margin-bottom: 35px;
}

.form__row {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;

  width: 100%;

  margin-bottom: 22px;

  &:last-child {
    width: auto;
    margin: 0 -10px;
    margin-top: 40px;
  }

  & .button {
    flex-grow: 1;

    margin: 0 10px;
    padding: 10px 5px;

    min-width: 120px;

    border-radius: 15px;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  &--nowrap {
    align-items: stretch;
    flex-wrap: nowrap;
  }
}

.form__text {
  color: #848484;
  font-weight: 500;

  margin-right: auto;
}

.form__input {
  border: none;

  padding: 20px 20px;

  background-color: #f5f5f5;
  border-radius: 10px;

  width: 100%;
  max-width: 335px;

  box-sizing: border-box;

  outline: none;

  &:focus {
    background-color: rgb(207, 207, 207);
  }
}

.form__mobile {
  margin-top: -30px;
  margin-bottom: 25px;
}

.form__mobile-list,
.form__mobile-list .acc__sub-list {
  margin: 0;
  padding: 0;
  list-style: none;

  color: #fff;

  border-radius: 20px;
  background-color: @color-green;

  padding: 15px 30px;
}

.form__mobile-item,
.form__mobile-sub-item {
  &:not(:first-child) {

    border-top: 1px solid rgba(255, 255, 255, 0.5);
  }
}

.form__mobile-sub-item {
  &:not(:first-child) {
    margin-top: 10px;
    padding-top: 10px;
  }
}

.form__mobile-list .acc__button {
  padding: 0;
  border: none;
  display: block;
  position: relative;

  width: 100%;

  color: #fff;
  font-family: inherit;
  font-size: 16px;
  text-align: left;

  background-color: rgba(0, 0, 0, 0);
  margin: 0;
  padding: 10px 0;
  padding-right: 15px;

  outline: none;

  &::before {
    content: "";
    position: absolute;
    display: block;

    width: 15px;
    height: 15px;

    top: 50%;
    right: 0px;
    transform: translateY(-50%) rotate(180deg);

    transition: transform 0.2s;

    background-image: url("../images/icons/back.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }

  &.active {
    &::before {
      transform: translateY(-50%) rotate(270deg);
    }
  }
}

.form__mobile-list .acc__sub-list {
  padding: 0;
  padding-left: 20px;

  margin: 10px 0;
}

.form__mobile-link {
  display: block;
  color: #fff;
  text-decoration: none;
}

@media (min-width: @tablet-width) {
  .form__mobile {
    display: none;
  }

  .form {
    padding: 45px 55px;

    &--mobile-hiden {
      display: block;
    }
  }

  .form__row {

    & .button {
      min-width: 200px;
    }
  }
}