.popup {
    position: fixed;
    z-index: 9999;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100vw;
    height: 100vh;

    top: 0;

    background-color: rgba(0, 0, 0, 0.3);

    &.remove {
        animation: popupWrapperHide 0.3s cubic-bezier(.13, .3, .17, .99);

        & .popup__content {
            animation: popupHide 0.3s cubic-bezier(.13, .3, .17, .99);
        }
    }
}

.popup__content {
    position: relative;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    padding: 20px;
    padding-top: 60px;
    border-radius: 15px;

    margin: 0 10px;

    background-color: #fff;

    animation: popupShow 0.3s cubic-bezier(.13, .3, .17, .99);

    & .button {
        margin: 10px;
    }

    & .button-close {
        position: absolute;

        top: 5px;
        right: 5px;
    }
}

@keyframes popupShow {
    0% {
        opacity: 0;
        visibility: hidden;
        transform: translate(-50%, -50%) scale(1.8);
    }

    100% {
        visibility: visible;
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }
}

@keyframes popupHide {
    0% {
        opacity: 1;
        visibility: visible;
        transform: translate(-50%, -50%) scale(1);
    }

    100% {
        opacity: 0;
        visibility: hidden;
        transform: translate(-50%, -50%) scale(1.8);
    }
}

@keyframes popupWrapperShow {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes popupWrapperHide {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}