.button-bureger {
  border: none;
  background-color: rgba(0, 0, 0, 0);
  padding: 0;

  display: block;
  position: absolute;

  width: 46px;
  height: 30px;

  top: 15px;
  right: 5px;

  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    display: block;

    width: 30px;
    height: 3px;

    top: 7px;
    left: 8px;

    background-color: #333;

    transition: transform 0.3s, box-shadow 0.3s, top 0.3s;
  }

  &::after {
    top: 14px;

    box-shadow: 0 7px 0 0 #333;
  }

  &.active {
    &::before {
      top: 14px;
      transform: rotate(45deg);
    }

    &::after {
      top: 14px;
      transform: rotate(-45deg);

      box-shadow: 0 0 0 0 #333;
    }
  }
}

.button {
  text-decoration: none;
  display: inline-block;
  min-width: 240px;
  color: @color-green-text;
  font-size: 16px;
  text-align: center;
  font-family: inherit;
  padding: 18px 20px;
  border-radius: 30px;
  border: 1px solid @color-green;
  background-color: #fff;
  box-sizing: border-box;
  transition: opacity .3s ease-in-out;
  outline: none;
  cursor: pointer;
  @media only screen and (max-width: 330px) {
    font-size: 14px;
  }

  &:hover,
  &:focus {
    color: darken(@color-green-text, 15%);
    border-color: darken(@color-green, 20%);
  }

  &:active,
  &.active {
    color: #fff;
    background-image: @gradient-green;
    border-color: @color-green;
  }

  &__big {
    max-width: 320px;
    min-width: 320px;
    width: 100%;
    height: 100%;
    min-height: 60px;
    max-height: 40px;
    padding-top: 0;
    padding-bottom: 0;
    display: inline-flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    img {
      margin-right: 15px;
      @media only screen and (max-width: 340px) {
        width: 25px;
      }
    }
  }

  &__middle {
    max-width: 240px;
    min-width: auto;
    width: 100%;
    height: 100%;
    min-height: 60px;
    max-height: 40px;
  }

  &__small {
    max-width: 220px;
    min-width: auto;
    width: 100%;
    height: 100%;
    min-height: 60px;
    max-height: 40px;
  }

  &__little {
    max-width: 220px;
    min-width: auto;
    width: 100%;
    height: 100%;
    min-height: 60px;
    max-height: 40px;
    padding-top: 0;
    padding-bottom: 0;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;

    img {
      margin-right: 15px;
    }
  }

  &--red {
    color: #fff;
    border-color: @color-red;
    background-image: @gradient-red;

    &:hover,
    &:focus,
    &:active,
    &.active {
      opacity: 0.8;

      color: #fff;
      border-color: @color-red;
      background-image: @gradient-red;
    }
  }

  &--back {
    min-width: 0;
    max-width: 155px;
    border-radius: 10px;

    color: #fff;

    padding: 9px 45px;

    background-image: url("../images/icons/back.svg"), @gradient-green;
    background-repeat: no-repeat, repeat;
    background-position: 18px center, center;
    background-size: 13px;

    &:hover,
    &:focus,
    &:active,
    &.active {
      opacity: 0.9;
      color: #fff;
      border-color: @color-green;
      background-image: url("../images/icons/back.svg"), @gradient-green;
    }

  }

  &--green {
    color: #fff;

    background-image: @gradient-green;

    &:hover,
    &:focus,
    &:active {
      opacity: 0.9;
      color: #fff;
      border-color: @color-green;
      background-image: @gradient-green;
    }

    &:active {
      opacity: 0.95;
    }
  }

}

.button-close {
  padding: 0;
  border: none;
  background-color: #fff;
  position: relative;

  display: block;

  width: 45px;
  min-width: 45px;
  height: 45px;

  outline: none;

  background-image: url("../images/icons/close.svg");
  background-repeat: no-repeat;
  background-size: 23px;
  background-position: center;

  cursor: pointer;

  transition: opacity 0.2s;

  &:hover,
  &:focus,
  &:active {
    opacity: 0.9;
  }
}

.button-logout {
  padding: 0;
  border: none;
  display: block;

  width: 45px;
  height: 62px;

  background-image: url("../images/icons/logout.svg");
  background-repeat: no-repeat;
  background-size: 18px;
  background-position: center;
}

.button__label {
  cursor: pointer;

  & .button {
    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;

    border-radius: 0;
    border-color: #a1a1a1;
    border-top: none;
    border-bottom: none;
    border-left: none;

    &:hover,
    &:focus,
    &:active {
      color: @color-green-text;
      background: #fff;
    }
  }

  .button-checkbox {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    border-radius: 0;
    border-color: @grey-seven;
    border-top: none;
    border-bottom: none;
    border-left: none;
    min-width: auto;
    padding: 16px 30px 16px 0;

    &:hover,
    &:focus,
    &:active {
      color: @color-green-text;
      background: #fff;
    }
  }

  & input {
    display: none;

    &:checked ~ .button .checkbox__indicator {

      &::after,
      &::before {
        display: block;
      }
    }
  }
}


.checkbox__indicator {
  position: relative;

  display: block;

  width: 18px;
  height: 18px;

  border-radius: 3px;
  border: 1px solid @color-green;

  margin-right: 15px;

  &::before {
    content: "";
    position: absolute;
    display: none;

    width: 18px;
    height: 1px;
    top: 5px;
    right: -6px;
    transform: rotate(-45deg);

    background-color: @color-green;
    box-shadow: 0 0 0 2px #fff;
  }

  &::after {
    content: "";
    position: absolute;
    display: none;

    width: 8px;
    height: 1px;
    top: 9px;
    left: 2px;
    transform: rotate(45deg);

    background-color: @color-green;
  }
}

@media (min-width: 1212px) {
  .button-bureger {
    display: none;
  }
}

