.button-fixed {
  background: @color-red;
  border: 2px solid @color-red;
  border-radius: 50%;
  box-shadow: 0 8px 10px @red-rgba-two;
  cursor: pointer;
  height: 68px;
  text-align: center;
  width: 68px;
  position: fixed;
  right: 3%;
  bottom: 7%;
  z-index: 999;
  transition: .3s;
  -webkit-animation: review linear 1s infinite;
  animation: review linear 1s infinite;

  .text-call {
    height: 68px;
    width: 68px;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    align-items: center;
    justify-content: center;

    span {
      text-align: center;
      color: @color-red;
      opacity: 0;
      font-size: 0;
      position: absolute;
      left: 50%;
      top: 22px;
      transform: translateX(-50%);
      line-height: 14px;
      font-weight: 600;
      text-transform: uppercase;
      transition: opacity .3s linear;
      font-family: @font-montserrat;
      margin-left: -2px;
    }

    &:hover span {
      opacity: 1;
      font-size: 10px;
    }
  }

  &:hover {
    img {
      display: none;
      transition: .3s;
    }

    z-index: 1;
    background: #fff;
    transition: .3s;
  }

  img {
    height: 37px;
    margin: 5px 0 5px -4px;
    animation: opsimple 3s infinite;
    transition: .3s;
  }

  &:active {
    transform: scale(0.8);
    transition: .3s ease-in-out;
  }

}

@-webkit-keyframes review {
  0% {
    box-shadow: 0 8px 10px rgba(249, 92, 24, 0.3), 0 0 0 0 rgba(249, 92, 24, 0.2), 0 0 0 0 rgba(249, 92, 24, 0.2);
  }

  40% {
    box-shadow: 0 8px 10px rgba(249, 92, 24, 0.3), 0 0 0 15px rgba(249, 92, 24, 0.2), 0 0 0 0 rgba(249, 92, 24, 0.2);
  }

  80% {
    box-shadow: 0 8px 10px rgba(249, 92, 24, 0.3), 0 0 0 30px rgba(249, 92, 24, 0), 0 0 0 26.7px rgba(249, 92, 24, 0.067);
  }

  100% {
    box-shadow: 0 8px 10px rgba(249, 92, 24, 0.3), 0 0 0 30px rgba(249, 92, 24, 0), 0 0 0 40px rgba(249, 92, 24, 0);
  }
}

@keyframes review {
  0% {
    box-shadow: 0 8px 10px rgba(249, 92, 24, 0.3), 0 0 0 0 rgba(249, 92, 24, 0.2), 0 0 0 0 rgba(249, 92, 24, 0.2);
  }

  40% {
    box-shadow: 0 8px 10px rgba(249, 92, 24, 0.3), 0 0 0 15px rgba(249, 92, 24, 0.2), 0 0 0 0 rgba(249, 92, 24, 0.2);
  }

  80% {
    box-shadow: 0 8px 10px rgba(249, 92, 24, 0.3), 0 0 0 30px rgba(249, 92, 24, 0), 0 0 0 26.7px rgba(249, 92, 24, 0.067);
  }

  100% {
    box-shadow: 0 8px 10px rgba(249, 92, 24, 0.3), 0 0 0 30px rgba(249, 92, 24, 0), 0 0 0 40px rgba(249, 92, 24, 0);
  }
}

@keyframes opsimple {
  0% {
    opacity: 0;
  }

  40% {
    opacity: 1;
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@-webkit-keyframes opsimple {
  0% {
    opacity: 0;
  }

  40% {
    opacity: 1;
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

