.user-nav {
    // border-top: 2px solid #f2f6f7;
}

.user-nav__list {
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    justify-content: flex-end;
}

.user-nav__item {
    display: flex;
    align-items: center;
}


.user-nav__link {
    display: block;

    width: 45px;
    height: 62px;
    
    &--notifications {
        background-image: url("../images/icons/notification.svg");
        background-repeat: no-repeat;
        background-size: 20px;
        background-position: center;

        &.active {
            position: relative;

            &::before {
                content: "";
                position: absolute;
                display: block;

                width: 7px;
                height: 7px;

                top: 29%;
                right: 26%;

                border-radius: 50%;
                background-color: @color-red;
                border: 2px solid #fff;
            }
        }
    }
}

.user-nav__button {
    position: relative;

    padding: 0;
    border: none;
    background-color: #fff;

    display: none;
    align-items: center;

    color: #000;
    font-weight: 500;

    width: 100%;
    max-width: 320px;
    height: 80px;

    padding-left: 35px;
    // padding-right: 60px;

    box-sizing: border-box;
}

.user-nav__photo-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 46px;
    min-width: 46px;
    height: 46px;

    border: 3px solid @color-green;
    background-color: #1a1a1a;
    border-radius: 50%;

    margin-right: 15px;

    overflow: hidden;
}

.user-nav__photo {
    width: 100%;
}

.user-nav__dropdown {
    position: absolute;
    z-index: 9;

    bottom: -50px;
    right: 0;

    padding: 0;
    margin: 0;
    list-style: none;

    width: 221px;

    padding: 0;

    background-color: #fff;
    // border-radius: 5px;
    // box-shadow: 0 3px 10px 1px rgba(0,0,0,0.2);
}

.user-nav__dropdown-link {
    display: block;
    padding: 15px 15px;

    color: @color-green;
    font-size: 16px;
    text-decoration: none;
    text-align: center;

    // transition: color 0.2s, background 0.2s;

    &:hover,
    &:focus {
        color: #fff;
        background-image: @gradient-green;
    }

    &--logout {
        background-image: url("../images/icons/logout.svg");
        background-repeat: no-repeat;
        background-size: 17px;
        background-position: 25% center;

        &:hover,
        &:focus {
            color: #fff;
            background-image: url("../images/icons/logout-white.svg"), @gradient-green;
            background-size: 17px, cover;
            background-position: 25% center, center;
        }
    }
}

@media (min-width: @tablet-width) {
    .user-nav__list {
        height: 100%;
    }
}

@media (min-width: 1212px) {
    .user-nav {
        border: none;
    }

    .user-nav__item {
        border-left: 2px solid #f2f6f7;
    }

    .user-nav__button {
        &::before {
            top: 46%;
        }
    }

    .user-nav__button {
        display: flex;
    }

    .user-nav__link {
        width: 75px;
        height: 80px;

        &--notifications {
            &.active {
                &::before {
                    top: 34%;
                    right: 36%;
                }
            }
        }
    }
}