.generate-progress(@n, @i: 0) when (@i =< @n) {
  .progress progress[value="@{i}"]
  {
    & + .progress-value:before { content : '@{i}%' }
    & ~ .progress-bg .progress-bar { width: @i * 1% }
  }
  .generate-progress(@n, (@i + 1));
}

.generate-progress(100);

.progress {
  font: 12px Arial, Tahoma, sans-serif;
  position: relative;
  overflow: hidden;
  width: 220px;
  margin: auto;
  @media only screen and (max-width: 640px) {
    max-width: 320px;
    min-width: 320px;
  }
  @media only screen and (max-width: 399.98px) {
    width: 100%;
    max-width: 100%;
    min-width: 100%;
  }
}

progress {
  position: absolute;
  width: 0;
  height: 0;
  overflow: hidden;
  left: -777px;
  display: block;
}

.progress-value {
  color: @white;
  display: block;
  line-height: 21px;
  text-align: center;
  font-family: @font-montserrat;
  font-size: 11px;
  font-weight: 500;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 99;
  text-shadow: 0 0 1px @black-two;
}

.progress-bg {
  background: @grey-two;
  position: relative;
  height: 12px;
  border-radius: 5px;
  overflow: hidden;
}

@keyframes progress_bar {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -40px 0;
  }
}

.progress-bar {
  overflow: hidden;
  background: @green-one;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  transition: width 1s linear;
  &:after {
    animation: progress_bar 0.8s linear infinite;
    background-image: linear-gradient(45deg,rgba(255,255,255,.25) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.25) 50%,rgba(255,255,255,.25) 75%,transparent 75%,transparent);
    background-size: 40px 40px;
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}