.beta-message {
  width: 100%;
  background: #E0E0E0;
  text-align: center;
  font-size: 16px;
  color: #666666;
  line-height: 1.8em;
  padding-left: 15px;
  padding-right: 15px;

  @media only screen and (max-width: 1699.98px) {
    margin-top: 0;
    padding-left: 10%;
    padding-right: 10%;
  }
  @media only screen and (max-width: 1211.98px) {
    margin-top: 62px;
    padding-left: 15px;
    padding-right: 15px;
  }
  @media only screen and (max-width: 479.98px) {
    font-size: 14px;
  }

}