.header {
    position: fixed;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    z-index: 99;
    background-color: #fff;

    top: 0;
    left: 0;

    width: 100%;
    min-height: 60px;

    box-shadow: 0 8px 40px 0 rgba(0,0,0, 0.1);

    &--inner {
        & .header__logo-wrapper {
            display: none;
        }

        & .button-bureger {
            display: none;
        }

        & .header__user-nav {
            display: none;
        }
    }

    &--info {
        & .header__title {
            display: none;
        }
    }
}

.header__button-back {
    display: block;

    width: 35px;
    height: 62px;

    margin-left: 15px;
    margin-right: auto;

    background-image: url("../images/icons/header-back.svg");
    background-repeat: no-repeat;
    background-size: 30px;
    background-position: center;
}

.header__logo-wrapper {
    padding: 15px 15px;
    padding-bottom: 14px;
    margin-right: auto;
}

.header__title {
    position: absolute;
    margin: 0;

    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;

    font-size: 24px;
    font-weight: 500;
}

.header__logo {
    width: 150px;
}

.header__collapse {
    flex-grow: 1;

    overflow: hidden;
    // width: 100%;
    height: auto;
    max-height: 0;

    transition: max-height 0.3s;
}

.header__nav {
    max-width: 1212px;
}

.header__button-search {
    padding: 0;
    border: none;
    background-color: #fff;
    display: block;

    width: 62px;
    height: 62px;

    outline: none;

    background-image: url("../images/icons/search.svg");
    background-repeat: no-repeat;
    background-size: 20px;
    background-position: center;
}

.header__search-wrapper {
    position: absolute;
    display: flex;
    align-items: center;

    top: 0;
    right: 0;

    width: 80%;
    height: 62px;

    transform: translateY(-110%);

    transition: transform 0.2s;

    background-color: #fff;

    &.active {
        transform: translateY(0)
    }
}

.header__search {
    border: none;

    padding: 13px 17px;
    padding-right: 40px;

    width: ~"calc(100% - 50px)";

    background-color: #fff;
    border-radius: 10px;
    margin-right: auto;

    outline: none;

    background-image: url("../images/icons/search.svg");
    background-repeat: no-repeat;
    background-size: 17px;
    background-position: 95% center;

    box-sizing: border-box;

    &:focus {
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
    }
}

.header__user-nav {
    position: absolute;

    top: 0;
    right: 51px;
}

