@media (min-width: 1212px) {
    .header__button-search {
        display: none;
    }

    .header__search-wrapper {
        display: none;
    }
    .header__collapse {
        max-height: 300px;
        flex-grow: 1;

        display: flex;
        justify-content: space-between;
    }

    .header {
        position: relative;
        min-height: 80px;
        justify-content: space-between;
        flex-wrap: nowrap;

        &--inner {
            & .header__logo-wrapper {
                display: block;
            }

            & .header__user-nav {
                display: block;
            }
        }
    }

    .header__button-back {
        display: none;
    }

    .header__logo-wrapper {
        padding: 14px 33px;
        margin-right: 0;
    }

    .header__logo {
        width: auto;
    }

    .header__title {
        display: none;
    }

    .header__nav {
        flex-grow: 1;
        margin: 0 auto;
    }

    .header__user-nav {
        position: relative;
    
        top: 0;
        right: 0;
    }
}