.attach__wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.attach__head {
  text-transform: uppercase;
  font-weight: 700;
  padding: 0 0 5px 0;
  margin: 0 0 5px 0;
}

.attach__item {
  display: flex;
  padding: 0 0 0 0;
  margin: 0 0 0 0;
  width: 100%;
  justify-content: center;
  transition: .5s ease-in-out;

  &:last-of-type {
    margin-bottom: 0;
    border-bottom: none;
  }

  label {
    padding-bottom: 30px;
    transition: .3s ease-in-out;
  }
}

.attach__button {
  @media only screen and (max-width: 399.98px) {
    width: 100%;
  }
}

.attach_mt {
  margin-top: 10px;
}

.attach__up {
  cursor: pointer;
}

.attach__edit {
  display: none;
  margin-right: 10px;
  cursor: pointer;
  transition: .2s;

  a {
    text-decoration: none;

    &:hover {
      text-decoration: none;
    }
  }

  img {
    width: 20px;
    height: 20px;
    @media only screen and (max-width: 600px) {
      width: 27px;
      height: 27px;
    }
  }

  &:hover {
    a {
      text-decoration: none;
    }
  }
}

.attach__delete {
  display: none;
  cursor: pointer;

  a {
    text-decoration: none;

    &:hover {
      text-decoration: none;
    }
  }

  img {
    width: 20px;
    height: 20px;
    @media only screen and (max-width: 600px) {
      width: 27px;
      height: 27px;
    }
  }

  &:hover {
    a {
      text-decoration: none;
    }
  }
}

.attach__item--attached {
  transition: .5s ease-in-out;

  &:nth-child(n+2) {
    padding-top: 20px;
  }

  .attach__up {
    display: none;
  }

  .attach__name {
    width: 60%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: left;
    @media only screen and (max-width: 600px) {
      line-height: 30px;
    }
  }

  .attach__edit {
    display: inline-block;
    margin-left: auto;
    margin-right: 30px;
    @media only screen and (max-width: 339.98px) {
      margin-right: 0;
    }
  }

  .attach__delete {
    display: inline-block;
    margin-left: 20%;
  }

  .attach__button {
    @media only screen and (max-width: 399.98px) {
      width: 0;
    }
  }
}

.attach__input {
  display: none;
}
