.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow: hidden;
  padding: 20px 10px;
  border-radius: 25px;
  border: 2px solid @color-red;
  background-color: #fff;
  box-shadow: 0 1px 3px @black-rgba-one;
  margin: 0 auto 40px;
  transform: translateY(0) scale(1);
  transition: .3s ease-in-out;

  &:hover {
    @media only screen and (min-width: 1280px) {
      box-shadow: 0 15px 30px @black-rgba-two;
      transform: translateY(-2px) scale(1.02);
    }
  }

  @media only screen and (max-width: 860px) {
    padding: 20px 90px;
  }
  @media only screen and (max-width: 768px) {
    padding: 30px 70px;
  }
  @media only screen and (max-width: 520px) {
    padding: 60px 15px 30px;
  }

  .task-icon {
    border-radius: 0 22px 0 25px;
  }
}

.info__text {
  margin: 0;
  color: @color-ash;
  font-size: 14px;
  line-height: 150%;
  text-align: center;
  font-weight: 500;
  max-width: 740px;
  margin-bottom: 28px;
  transition: .3s ease-in-out;

  @media only screen and (min-width: 480px) {
    font-size: 16px;
    line-height: 150%;
  }

  @media only screen and (min-width: 640px) {
    font-size: 18px;
  }

  @media only screen and (min-width: 1024px) {
    font-size: 24px;
  }
}

.info__button {
  font-size: 16px;
  padding: 20px 20px;
  font-weight: 500;
}

.info__back {
  display: none;
}

@media (min-width: @desktop-width) {
  .info__back {
    display: inline-block;
  }

  .info {
    padding: 40px 10px;
  }
}