*, ::after, ::before {
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
}

body {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100vh;
  font-family: 'Montserrat', sans-serif;
  color: @color-ash;
  font-size: 16px;
  line-height: 1.2;
  background-color: #f1f1f1;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

button,
input {
  font-family: inherit;
}

dialog {
  border: 0;
  background: none;
  padding: 0;
  right: auto;
}

fieldset {
  border: 0;
  padding: 0;
}

input:active,
input:focus,
button:active,
button:focus,
a:active,
a:focus {
  outline: none;
}

img, a {
  border: none;
}

.breadcrumbs {
  display: none;
  @media only screen and (min-width: 0px) and (max-width: 1211.98px) {
    display: block;
  }
  ul {
    list-style: none;
    padding-left: 0;
    padding-bottom: 25px;
    margin: 0;
    li {
      display: inline;
      font-size: 18px;
      font-weight: 500;
      @media only screen and (max-width: 479.98px) {
        font-size: 14px;
      }
      &:nth-last-child(1) {
        a {
          color: @green-one;
        }
      }
      + li::before {
        padding: 8px 5px;
        color: @color-ash;
        content: "/\00a0";
      }
      a {
        color: @color-ash;
        text-decoration: none;
        transition: .3s;
        &:hover {
          color: @green-one;
        }
      }
    }
  }
}

textarea {
  resize: none;
}

::-webkit-input-placeholder { /* Chrome */
  color: @grey-one;
  font-size: 18px;
  font-family: @font-montserrat;
  font-weight: 400;
  transition: .3s ease-in-out;
  @media only screen and (max-width: 1599.98px) {
    font-size: 18px;
  }
  @media only screen and (max-width: 991.98px) {
    font-size: 14px;
  }
}

:-ms-input-placeholder { /* IE 10+ */
  color: @grey-one;
  font-size: 18px;
  font-family: @font-montserrat;
  font-weight: 400;
  transition: .3s ease-in-out;
  @media only screen and (max-width: 1599.98px) {
    font-size: 18px;
  }
  @media only screen and (max-width: 991.98px) {
    font-size: 14px;
  }
}

::-moz-placeholder { /* Firefox 19+ */
  color: @grey-one;
  font-size: 18px;
  font-family: @font-montserrat;
  font-weight: 400;
  transition: .3s ease-in-out;
  @media only screen and (max-width: 1599.98px) {
    font-size: 18px;
  }
  @media only screen and (max-width: 991.98px) {
    font-size: 14px;
  }
}

.separator {
  padding-left: 10px;
  padding-right: 10px;
  color: @color-green;
  @media only screen and (max-width: 399.98px) {
    display: none;
  }
}

